.section {
  max-width: 100%;
  padding: 0 20px;
  margin: 15px 0;
  box-sizing: border-box;

  .title {
    border-bottom: 1px solid #ffffff;
    margin-bottom: 10px;

    h3 {
      font-size: 1.2rem;
      text-transform: uppercase;
    }
  }

  .content {
    min-width: 185px;
  }
}