.form-text-area {
  margin: 10px 0;

  label {
    font-size: 1.2rem;
  }

  textarea {
    width: 100%;
    padding: 0.5rem;
    box-sizing: border-box;
    margin-top: 5px;
    outline: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 150px;
    resize: none;
  }
}